.pw-image-preview{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.9);z-index:1000}.pw-image-preview>span{position:absolute;top:10px;left:0;width:100%;color:#fff;text-align:center;z-index:1}.pw-image-preview>div{position:absolute;width:100%;height:100%;white-space:nowrap}.pw-image-preview>div>div{display:inline-block;width:100%;height:100%;-webkit-transform:translateZ(0);transform:translateZ(0)}.pw-image-preview>div>div>img{width:100%;height:100%;-o-object-fit:contain;object-fit:contain}